<template>
<!--  -->
  <div>
    <!-- 头部 -->
    <header class="fl-ex al-it">
      <router-link to="/works" tag="img" src="../../../static/img/29.png" alt="图"></router-link>
      <p>活动管理</p>
    </header>
    <!-- 工会信息 -->
    <main v-for="(item,index) in list" :key="'info1-'+index">
      <div class="chairman">
        <div class="list" v-for="(s,index) in item.lists" :key="index">
          <div class="fl-ex al-it ju-be">
            <p>{{s.p}}</p>
            <span>{{s.span}}</span>
          </div>
        </div>
        <div style="width:100%; background-color:white;" v-for="(m,index) in item.img" :key="'info2-'+index">
          <div class="fl-ex al-it ju-be">
            <p>{{m.p}}</p>
            <span>{{m.span}}</span>
          </div>
          <a>{{m.wenzi}}</a>
          <div class="fl-ex al-it ju-be">
            <p>{{m.p1}}</p>
            <span>{{m.span1}}</span>
          </div>
          <img :src="m.pic" />
        </div>
      </div>
    </main>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [
        {
          lists: [
            {
              p: "姓名",
              span: "李兰"
            },
            {
              p: "类型",
              span: "求职者"
            },
            {
              p: "行业",
              span: "无"
            },
            {
              p: "联系方式",
              span: "15897230294"
            },
            {
              p: "发布日期",
              span: "2018-02-05"
            },
            {
              p: "所在地",
              span: "西宁市体育场"
            },
          ],
          img: [
            {
              p: "介绍",
              span: "",
              p1: "资料",
              span1: "",
              wenzi:'本人性格开朗，稳重，有活力，待人真诚；工作认真负责，积极主动，能吃苦耐劳，勇于承受压力，用于创新；有很强的组织能力和团队协作精神，具有较强的适应能力；纪律性强，工作积极配合；意志坚强。具有较强的奉献精神。对待工作认真负责，善于沟通，协调有较强的组织能力与团队精神；活泼开朗，乐观上进，有爱心并善于施教并行；上进心强，勤于学习能不断提高自身的能力与综合素质。在未来的工作中，我将以充沛的活力，刻苦专研的精神来努力工作，稳定地提高自己地工作能力，与企业共同发展。',
              pic: "../../../static/img/43.png"
            },
          ]
        },
      ]
    };
  }
};
</script>

<style scpoed>
header {
  width: 100%;
  height: 3.2rem;
  background-color: #df0001;
}
header img {
  width: 4%;
  height: 1.25rem;
  margin-left: 5%;
}
header p {
  font-size: 1.2rem;
  color: white;
  margin-left:32%;
}
/* 工会信息  */
main {
  width: 100%;
}
main .chairman {
  width: 100%;
  background-color: #e2e2e2;
}
main .chairman img {
  width: 30%;
  height: 10rem;
  margin-left: 3%;
  margin-top: 5%;
  padding-bottom: 3%;
}
main .chairman .list {
  width: 100%;
  height: 2.5rem;
  background-color: white;
}
main .chairman:last-child {
  padding-bottom: 0.5rem;
}
main .chairman > div > div {
  width: 94%;
  height: 2rem;
  margin: 0 auto;
  border-bottom: 0.05rem solid #e2e2e2;
}
main .chairman > div > div p {
  font-size: 0.8rem;
  color: #8c8c8c;
}
main .chairman > div > div span {
  font-size: 0.8rem;
  color: #393939;
}
main a {
  font-size: 0.87rem;
  line-height: 1.3rem;
  text-indent: 2rem;
  letter-spacing: 0.05rem;
  text-align: left;
  width: 95%;
  margin: 0 auto;
  color: #313131;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 1rem;
}
</style>


